.navigation-item {
  margin: 10px 10px;
  display: block;
  width: 100%;

  &:hover, &:active, &.active {
    color: #40a4c8;
  }

  @media (min-width: 630px) {
    margin: 0 10px;
    display: flex;
    width: auto;
    align-items: center;
    justify-items: center;

  }


  a {
    color: #000;
    padding: 16px 10px;
    text-decoration: none;
    width: 100%;
    display: block;
    transition: all .3s linear;

    @media (min-width: 630px) {
      color: #000;
      text-decoration: none;
      height: 100%;
      display: block;

      &:hover, &:active, &.active {
        color: #E4572E;

      }
    }

    &:hover, &:active, &.active {

      @media (min-width: 630px) {
        background-color: #fff;
        transform: scale(1.1);
        border-bottom: 1px solid #E4572E;
      }
    }
  }
}

